import { mapActions } from 'vuex'

export default {
    data: () => ({
        timer: 0,
        timer_logout: 0
    }),
    computed: {
    },
    watch: {
        timer(){
            this.timer_logout -= 1
        },
        timer_logout(value){
            if(value === 0) this.ACT_APP_LOGOUT()
        }
    },
    created(){
    },
    mounted(){
        this.timer_logout_set()
        this.timer_logout_start()
    },
    updated(){
    },
    destroyed(){
        this.timer_logout_stop()
    },
    methods:{
        ...mapActions([
            'ACT_APP_LOGOUT',
        ]),
        timer_logout_set(){
            this.timer = 0
            this.timer_logout = process.env.VUE_APP_AUTO_LOGOUT_TIME
        },
        timer_logout_start() {
            this.interval_logout_Timer = setInterval(() => {
                this.timer += 1
            }, 1000)
        },
        timer_logout_stop(){
            clearInterval(this.interval_logout_Timer)
        },
    }
}
