<template>
    <v-container fluid class="container fill-height" @click="timer_logout_set()">
        <div class="close_app_btn_area">
            <v-btn @click="programCloseDialog = true" text class="close_app_btn">
                close
            </v-btn>
        </div>
        <v-col align="center" justify="center">
            <v-row justify="center">
                <img src="@/assets/images/common/icon_bi.png" class="logo"/>
            </v-row>
            <v-row align="start" justify="center">
                <main-menu-item 
                    index="0" 
                    :icon="menuList[0].icon" 
                    :text="menuList[0].text" 
                    :link="menuList[0].link" 
                    :type="menuList[0].type" 
                />
                <div class="menu-item-group">
                    <template v-for="(menu, index) in menuList">
                        <main-menu-item
                            v-if="index > 0"
                            :key="index" 
                            :index="index" 
                            :icon="menu.icon" 
                            :text="menu.text" 
                            :link="menu.link" 
                            :type="menu.type" 
                        />
                    </template>
                </div>
            </v-row>
        </v-col>
        <common-confirm-dialog
            :dialog="programCloseDialog"
            :dialogItems="programCloseDialogItems"
            v-on:close-cancel="CloseDialog()"
            v-on:close-allow="CloseApp()"
        />
    </v-container>
</template>

<script>
import timer_logout from '@/mixins/timer_logout.js'
import site from '@/mixins/site.js'

export default {
    name: 'Main',
    mixins:[ timer_logout, site ],
    data:()=>({
        menuList: [],
        programCloseDialog: false,
        programCloseDialogItems:{
            title: '프로그램 종료',
            text: 'SMART MIRROR를 종료하시겠습니까?',
            close: '취소',
            confirm: '확인'
        },
    }),
    computed: {
    },
    watch:{
    },
    created(){
        // 현재 site 확인
        const site = process.env.VUE_APP_SITE
        this.menuList = this.site_menu_list(site)
    },
    mounted(){
    },
    methods: {
        CloseDialog(){
            this.programCloseDialog = false
        },
        CloseApp(){
        }
    },
}
</script>

<style scoped lang="scss">
.container {
    background-color: black;
    color: white;
    animation: fadein 2s;        

    @keyframes fadein {
        from {
            opacity:0;
        }
        to {
            opacity:1;
        }
    }    
}
.menu-item-group {
    display: inline-block;
    width: 620px;
}
.logo{
    margin-bottom: 5rem;
}
</style>